import React, {useEffect} from 'react';
import '../../App.css';
import {AppBar, Button, CircularProgress, Container, IconButton, Toolbar, Typography} from "@mui/material";
import {useAppSelector} from "../../store/store";
import {Menu,} from "@mui/icons-material";
import LinearProgress from '@mui/material/LinearProgress';
import {initializeApp, RequestStatusType} from "../../store/app-reducer";
import {ErrorSnackbar} from "../ErrorSnackbar/ErrorSnackbar";
import {Route, Routes, Navigate} from 'react-router-dom'
import {Login} from "../Login/Login";
import {TodosList} from "../Todolist/TodosList";
import {useDispatch} from "react-redux";
import {logoutTC} from "../../store/auth-reducer";

//C-R-U-D
function AppWithRedux() {

    const dispatch=useDispatch();
    const appStatuses = useAppSelector<RequestStatusType>(state => state.app.status)
    const isLoggedIn = useAppSelector<boolean>(state => state.login.isLoggedIn)
    const isInitialized = useAppSelector<boolean>(state => state.app.isInitialized)

    useEffect(()=>{
        dispatch(initializeApp())
    },[dispatch])

    if (!isInitialized) {
        return <div
            style={{position: 'fixed', top: '30%', textAlign: 'center', width: '100%'}}>
            <CircularProgress/>
        </div>
    }

    //UI:
    return (
        <div className="App">
            <ErrorSnackbar/>
            <AppBar position={'static'}>
                <Toolbar style={{justifyContent: 'space-between'}}>
                    <IconButton edge='start' color='inherit' aria-label='menu'>
                        <Menu/>
                    </IconButton>
                    <Typography variant='h6'>
                        Todolists
                    </Typography>
                    {isLoggedIn&& <Button onClick={()=>{dispatch(logoutTC())}} color='inherit' variant={'outlined'}>Logout</Button>}

                </Toolbar>
            </AppBar>
            {appStatuses === 'loading' && < LinearProgress color="secondary"/>}
            <Container fixed>
                <Routes>
                    <Route path='/' element={<TodosList/>}/>
                    <Route path='/login' element={<Login/>}/>
                    <Route path='/404' element={
                        <article>
                            <h1>ERROR 404</h1>
                            <h4 style={{textAlign: 'justify'}}>–û—à–∏–±–∫–∞ 404 –∏–ª–∏ Not Found (¬´–Ω–µ –Ω–∞–π–¥–µ–Ω–æ¬ª) ‚Äî —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω—ã–π –∫–æ–¥ –æ—Ç–≤–µ—Ç–∞ HTTP –æ —Ç–æ–º, —á—Ç–æ –∫–ª–∏–µ–Ω—Ç
                                –±—ã–ª –≤ —Å–æ—Å—Ç–æ—è–Ω–∏–∏ –æ–±—â–∞—Ç—å—Å—è —Å —Å–µ—Ä–≤–µ—Ä–æ–º, –Ω–æ —Å–µ—Ä–≤–µ—Ä –Ω–µ –º–æ–∂–µ—Ç –Ω–∞–π—Ç–∏ –¥–∞–Ω–Ω—ã–µ —Å–æ–≥–ª–∞—Å–Ω–æ –∑–∞–ø—Ä–æ—Å—É.
                                –û—à–∏–±–∫—É 404 –Ω–µ —Å–ª–µ–¥—É–µ—Ç –ø—É—Ç–∞—Ç—å —Å –æ—à–∏–±–∫–æ–π ¬´–°–µ—Ä–≤–µ—Ä –Ω–µ –Ω–∞–π–¥–µ–Ω¬ª –∏–ª–∏ –∏–Ω—ã–º–∏ –æ—à–∏–±–∫–∞–º–∏,
                                —É–∫–∞–∑—ã–≤–∞—é—â–∏–º–∏ –Ω–∞ –æ–≥—Ä–∞–Ω–∏—á–µ–Ω–∏–µ –¥–æ—Å—Ç—É–ø–∞ –∫ —Å–µ—Ä–≤–µ—Ä—É. –û—à–∏–±–∫–∞ 404 –æ–∑–Ω–∞—á–∞–µ—Ç, —á—Ç–æ –∑–∞–ø—Ä–∞—à–∏–≤–∞–µ–º—ã–π
                                —Ä–µ—Å—É—Ä—Å –º–æ–∂–µ—Ç –±—ã—Ç—å –¥–æ—Å—Ç—É–ø–µ–Ω –≤ –±—É–¥—É—â–µ–º, —á—Ç–æ –æ–¥–Ω–∞–∫–æ –Ω–µ –≥–∞—Ä–∞–Ω—Ç–∏—Ä—É–µ—Ç –Ω–∞–ª–∏—á–∏–µ –ø—Ä–µ–∂–Ω–µ–≥–æ
                                —Å–æ–¥–µ—Ä–∂–∞–Ω–∏—è.
                                –ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–∏ –Ω–∞–∏–±–æ–ª–µ–µ —á–∞—Å—Ç–æ —Å—Ç–∞–ª–∫–∏–≤–∞—é—Ç—Å—è —Å –æ—à–∏–±–∫–æ–π 404 –ø—Ä–∏ –ø–æ—Å–µ—â–µ–Ω–∏–∏ —Ç–∞–∫ –Ω–∞–∑—ã–≤–∞–µ–º—ã—Ö
                                ¬´–±–∏—Ç—ã—Ö¬ª –∏–ª–∏ ¬´–º—ë—Ä—Ç–≤—ã—Ö —Å—Å—ã–ª–æ–∫¬ª, —á—Ç–æ –¥–µ–ª–∞–µ—Ç, —Ç–∞–∫–∏–º –æ–±—Ä–∞–∑–æ–º, –æ—à–∏–±–∫—É 404 –æ–¥–Ω–æ–π –∏–∑ –Ω–∞–∏–±–æ–ª–µ–µ
                                —É–∑–Ω–∞–≤–∞–µ–º—ã—Ö –æ—à–∏–±–æ–∫ –≤ —Å–µ—Ç–∏ –ò–Ω—Ç–µ—Ä–Ω–µ—Ç</h4>
                        </article>
                    }/>
                    <Route path='*' element={<Navigate to='/404'/>}/>
                </Routes>
            </Container>
        </div>
    )
}

export default AppWithRedux;
